main{
    width: 90%;
    border: 1px solid black;
    margin: 10px auto;
    min-height: 1000px;
    height: 100%;
    -webkit-box-shadow: 2px 5px 25px 10px #000000; 
    box-shadow: 2px 5px 25px 10px #000000;
}

.ciclo__container{
    display: flex;
}
.ciclo__posts{
    display: flex;
    font-size: 26px;
    margin: 0;
    flex-direction: column;
}.ciclo__posts h3{
    margin: 0 0 0 10px;
    font-family: Arial, Helvetica, sans-serif;
    color: #202c2d;
    text-shadow: 0 1px #FFFFFF, -1px 0 #cdd2d5, -1px 2px #FFFFFF, -2px 1px #cdd2d5, -2px 3px #FFFFFF, -3px 2px #cdd2d5, -3px 4px #FFFFFF, -4px 3px #cdd2d5, -4px 5px #FFFFFF, -5px 4px #cdd2d5, -5px 6px #FFFFFF, -6px 5px #cdd2d5, -6px 7px #FFFFFF, -7px 6px #cdd2d5, -7px 8px #FFFFFF, -8px 7px #cdd2d5;
}
.ul__ciclos{
    padding-left: 8px; 
    width: 200px;
    margin: 0;
    height: auto;
    border-right: 1px solid black;
}

.post_container{
    display: flex;
    flex-direction: row;
    width: auto;
    margin: 5px;
    cursor: pointer;
}.post_container:hover h2,.post_container:hover p{
    transition: 300ms;
    opacity: 0.2;
}
.post_container h2{
    font-size: 22px;
    margin: 0 0 10px 0;
    font-family: 'Varela Round', sans-serif;
    transition: 300ms;
    color: white;
    text-shadow: 1px 1px 1px #000000;
}
.post_cover_text{
    display: inherit;
    flex-direction: column;
    transition: 300ms;
}.post_cover_text p{
    font-size: 16px;
    margin: 0 0 10px 0;
    font-family: Arial, Helvetica, sans-serif;
}
.post_image img{
    margin: 0 10px 0 0;
    border: 1px solid black;
    border-radius: 20px;
}
.post_date{
    opacity: 0.5;
    transition: 300ms;
}
.date-author_container{
    display: flex;
}
.date-author_container p{
    transition: 300ms;
    margin-left: 3px;
}
.post_author{
    opacity: 0.5;
}



/*SWEET ALERT*/


.swal-post-container{
    color: white;
}
div.swal2-popup{
    width: 800px;
    word-break: break-all;
    font-family: 'Varela Round', sans-serif;
}

.date-author_container-swal{
    margin: 0;
    display: flex;
    opacity: 0.7;
    justify-content: center;
    align-items: center;
}
.post_author-swal{
    margin: 0 0 0 20px;
}
.post_date-swal{
    margin: 0 0 0 0;
}
.title-swal{
    margin-bottom: 10px;
    text-decoration: underline;
}
.post-images{
    margin: 10px auto;
    display: flex;
    flex-direction: column;
    width: 75%;
    height: auto;
}







.telescope21-routes-container{
    margin: 0 auto;
    width: auto;
    justify-content: center;
}

.eachPost-container{
    margin: 20px;
}
.post-portada{
    cursor: pointer;
    border: 3px solid black;
    border-radius: 10px;
}
.post-name{
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    text-align: center;
}
.post-container{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    width: 1000px;
    margin: 0 auto;

}
.post-container-stopmotion{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    width: 100%;
    margin: 0 auto;
}
.double-author{
    border: 3px solid black;
    cursor: pointer;
}
.telescope-titles{
    width: fit-content;
    margin: 0 auto;
    font-family: Arial, Helvetica, sans-serif;
    color: #009696;
    text-align: center;
}.telescope-titles p{
    color: black;
    width: 80%;
    text-align: center;
    margin: 10px auto;
}


.li-telescope21{
    list-style: none;
    display: flex;
    justify-content: center;
    text-align: center;
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.li-telescope21 a{
    text-decoration: none;
    color: black;
    font-size: 16px;
    font-weight: 500;
    margin: 0;
    padding: 0;
}
.li-telescope21 a.active{
    color: #009696;
    text-decoration: underline 2px;
    transition: 200ms;
}
.li-telescope21 ul{
    padding: 0 20px 0 20px;
    border: 1px solid gray;
    border-top: none;
    border-bottom: none;
}

.portada-nosEscriben{
    width: auto;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    text-align: center;
}
.portada-nosEscriben p{
    width: 600px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.oneforcollage{
    border: 2px solid black;
}
.twoforcollage{
    border: 2px solid black;
}
.threeforcollage{
    border: 2px solid black;
}
.collage{
